<template>
  <div class="hello">
    <h1>{{ msg }}</h1>

<!-- =============== Bootstrap ======================= --> 

    <div class="alert alert-success alert-dismissible fade show" role="alert">
        With Bootstrap!
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
     </div>




<!-- =============== –û–±—â–∞—è –ø–∞–º—è—Ç—å ======================= --> 

    <div>–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ —Å—Ç–∞—Ç–µ–π: {{ getCount() }}</div>
    <div>
       <div><label for="postBody">–û—Ç–ø—Ä–∞–≤–∏—Ç—å –Ω–∞ —Å–µ—Ä–≤–µ—Ä Post Ajax</label></div>
       <input id="postBody" type="text" v-model="postBody" @change="sendPost">
    </div> 
    <div v-if="postedPosts().length">
        –°–æ—Ö—Ä–∞–Ω–µ–Ω–æ –≤ –æ–±—â–µ–π –ø–∞–º—è—Ç–∏:
    </div> 

    <div v-for="(item, index) in postedPosts()"  :key="'postedPosts'+index">
      {{index+1}}.  {{item}}
    </div>

    <div v-if="loading">Loading...</div>

    <ul v-if="errors && errors.length">
       <li v-for="(error, index) in errors" :key="index">
          {{error.message}}
       </li>
    </ul>
 
<!-- =============== –ª–æ–∫–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ ======================= --> 
   <div>&nbsp;</div>
    <div><label for="postLocalStorageBody">–î–æ–±–∞–≤–∏—Ç—å –≤ –ª–æ–∫–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ</label></div> 
    <input id="postLocalStorageBody" type="text" v-model="postLocalStorageBody" @change="sendLocalStorage">
    <button @click="—ÅlearLocalStorage()">–û—á–∏—Å—Ç–∏—Ç—å –ª–æ–∫–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ</button>
 
    <div v-for="(item, index) in postedLocalStorage" :key="index">
        {{index+1}}. {{item}}
    </div>


<!-- =============== –°—Ç–∞—Ç—å–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞ ======================= --> 
   <div>&nbsp;</div>
   <div>
    <button @click="getPosts()">–ü–æ–ª—É—á–∏—Ç—å —Å—Ç–∞—Ç—å–∏</button> 
    <button @click="ShowPosts()"><span v-if="showPosts">–°–ø—Ä—è—Ç–∞—Ç—å —Å—Ç–∞—Ç—å–∏</span><span v-else="">–û—Ç–∫—Ä—ã—Ç—å —Å—Ç–∞—Ç—å–∏</span></button> 
    <button @click="ClearPosts()">–û—á–∏—Å—Ç–∏—Ç—å —Å—Ç–∞—Ç—å–∏</button>
   </div>
    
    <transition name="slide-fade">
      <div v-show="showPosts">
        <ul v-if="posts && posts.length">
           <li v-for="(item, index) in posts" :key="index" >
              <p><b>{{item.title}}</b></p>
              <p>{{item.body}}</p>
           </li>
        </ul>
      </div>
    </transition>


  </div>
</template>

<script>
import {HTTP} from '../http-common'

export default {
  name: 'HelloWorld',
  components: {
  },
  data () {
    return {
       posts: [],
       errors: [],
       loading: false,
       postBody: '',
       showPosts: true,
       selected: null,
       postLocalStorageBody: '',
       postedLocalStorage: []
    }
  },
  props: {
    msg: {
      type: String,
      required: true
    }
  }, 
  methods: {
    ShowPosts() {
        return this.showPosts ? this.showPosts = false : this.showPosts = true
    },
    ClearPosts(){
        this.posts = [] 
    }, 
    getPosts() {
      this.loading = true
      this.posts = []
      HTTP.get('posts')
        .then(response => {
            this.posts = response.data
            console.log(this.posts)
          }
        )
        .catch(e => { this.errors.push(e)
        })
        .finally(() => (this.loading = false))
    },
    getCount() {
       return this.$store.state.count
    },
    postedPosts() {
       return this.$store.state.posts
    },
    sendPost() {
       this.loading = true
       this.$store.dispatch(
         {type: 'incrementAsync',
           amount: 1,
           text: this.postBody
         })

       HTTP.post('posts', {
          body: this.postBody
        })
        .then(response => {})
        .catch(e => { this.errors.push(e)
        })
        .finally(() => {
              this.loading = false
              this.postBody = ''
    	})
    },
    —ÅlearLocalStorage() {
        localStorage.posts = '[]'
        this.postedLocalStorage = []
    },
    localStoragePosts() {
        return JSON.parse(localStorage.posts)
    },
    sendLocalStorage() {
         this.postedLocalStorage.push(this.postLocalStorageBody)
         localStorage.posts = JSON.stringify(this.postedLocalStorage)
         this.postLocalStorageBody = ''
         console.log(localStorage.posts) 
    }
  },
  created() {
      if (localStorage.posts == '') {localStorage.posts = '[]'}
      this.postedLocalStorage =  this.localStoragePosts()
  }
}

</script>

